<template>
  <div>
    <!--  -->
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span></span>
      </div>
      <!--  -->
      <el-table
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column label="" width="120">
          <template slot-scope="scope">{{ scope.row.account }}</template>
        </el-table-column>
        <el-table-column prop="userGroup" label="" width="120"></el-table-column>
        <el-table-column prop="ctime" label="" show-overflow-tooltip></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button size="mini" @click="clickEdit(scope.$index, scope.row)"></el-button>
            <!-- - -->
            <el-dialog title="" :visible.sync="dialogFormVisible">
              <el-form :model="form">
                <el-form-item label="" :label-width="formLabelWidth">
                  <el-input v-model="form.name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                  <el-select v-model="form.region" placeholder="">
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                  </el-select>
                </el-form-item>
              </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button @click="cancelEdit"> </el-button>
                <el-button type="primary" @click="accEditSave"> </el-button>
              </div>
            </el-dialog>
            <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <div id="pagination">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[5, 10, 20, 50]"
          :page-size="10"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        ></el-pagination>
      </div>
      <!--  -->
      <div id="listBtn">
        <el-button @click="delchecked" class="delChecked" type="danger"></el-button>
        <el-button class="unChecked" type="primary"></el-button>
      </div>
    </el-card>
  </div>
</template>

<script>
// 
import { API_GET_USERLIST } from "@/API/apis.js";
import { API_DELETE_ACC } from "@/API/apis.js";
import { API_DELETE_CHECKED } from "@/API/apis.js";
import { API_EDIT_ACCOUNT } from "@/API/apis.js";
export default {
  data() {
    return {
      // -
      currentPage: 1,
      //
      total: 0,
      //
      pageSize: 5,
      // 
      tableData: [],
      multipleSelection: [],

      // 
      dialogFormVisible: false,
      //form
      form: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: ""
      },
      formLabelWidth: "120px",
      // ID
      editId: null
    };
  },
  methods: {
    // 
    clickEdit(index, row) {
      //id
      this.dialogFormVisible = true;
      //console.log(row.id);
      this.editId = row.id;
    },
    //
    accEditSave() {
      //  id acc usergroup
      //  this.editId  this.form.name  this.form.region
      API_EDIT_ACCOUNT(this.editId, this.form.name, this.form.region).then(
        res => {
          if (res) this.getuserlist(); //
          this.form.name = ""; //
          this.form.region = "";
        }
      );
      this.dialogFormVisible = false; //
      this.$message({
        message: "",
        type: "success",
        duration: 1000
      });
    },
    // 
    cancelEdit() {
      this.dialogFormVisible = false;
      this.form.name = ""; //
      this.form.region = "";
    },
    // 
    handleDelete(index, row) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          API_DELETE_ACC(row.id).then(res => {
            // console.log(res.data.code)
            if (res.data.code == 0) this.getuserlist();
          });
          this.$message({
            type: "success",
            message: "!",
            duration: 1500
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
            duration: 1500
          });
        });
        this.currentPage=1;
    },
    // 
    delchecked() {
      let arr = [];
      let ids = [];
      for (let item of this.multipleSelection) {
        arr.push(item.id);
        ids = JSON.stringify(arr);
      }
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          API_DELETE_CHECKED(ids).then(res => {
            if (res.data.code == 0) this.getuserlist();
          });
          this.$message({
            type: "success",
            message: "!",
            duration: 1500
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
            duration: 1500
          });
        });
        this.currentPage=1;
    },
    // 
    handleSizeChange(val) {
      this.pageSize = val;
      this.getuserlist();
    },
    // 
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getuserlist();
    },
    // 
    getuserlist() {
      API_GET_USERLIST(this.currentPage, this.pageSize).then(res => {
        if (res.statusText == "OK") {
          //
          //res.data.data.ctime=res.data.data.ctime.slice(1,10)
          this.total = res.data.total; // 
          this.tableData = res.data.data; //
          // 
          for (let i of this.tableData) {
            i.ctime = i.ctime.slice(0, 10); //
          }
        }
      });
    },
    // 
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    }
  },
  // Vuecreated,data,
  created() {
    //console.log(this.currentPage,this.pageSize);
    this.getuserlist(); //
  }
};
</script>

<style lang="less" scoped>
#pagination {
  margin-top: 15px;
}
#listBtn {
  margin-top: 15px;
}
</style>